import initIndex from '../../views/index.js';
import initDiagram from '../../views/components/ccosDiagram.js';
import initDiagramMobile from '../../views/components/ccosDiagramMobile.js';
import initSideNavigator from '../../views/components/sidenav.js';

initSideNavigator(
	document.getElementById('sidenavMenu'),
	document.getElementById('leftSidenavToggle'),
	document.getElementById('rightSidenavToggle')
);

if (window.pageId === 'index') {
	initIndex({
		experianLogoNode: document.getElementById('experianLogo'),
		experianDialogNode: document.getElementById('experianDialog'),
		rsiLogoNode: document.getElementById('rsiLogo'),
		rsiDialogNode: document.getElementById('rsiDialog'),
	});
}

if (window.pageId === 'ccos') {
	initDiagram(
		document.querySelectorAll('.step'),
		document.querySelectorAll('.step-info')
	);

	initDiagramMobile(
		document.querySelectorAll('.step-mobile'),
		document.querySelectorAll('.step-info-mobile')
	);
}



